/** Product Detail component styles **/
.product-detail {
  font-family: Inter;
  padding: 2.4rem 16rem 0 16rem;
}
.product-d-box {
  clear: both;
  max-width: 447px;
  margin: 48px auto;
  padding: 48px;
  background: #f0f2fe;
  border-radius: 4px;
}
.product-d-box textarea {
  width: 100%;
  border: 1px solid rgba(11, 29, 213, 0.3);
  background-color: inherit;
}
.product-d-box textarea:focus {
  outline: none;
}
.p-q {
  padding-top: 20px;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 1rem;
  gap: 1rem;
}
.p-q input[type="number"] {
  width: 100%;
  border-color: rgba(11, 29, 213, 0.3);
  background-color: inherit;
}

.p-d-header {
  font-weight: bold;
  font-size: 16px;
  line-height: 19px;
  color: #040b52;
  padding-bottom: 30px;
}
form {
  font-weight: 500;
  font-size: 14px;
  margin-bottom: 17px;
}
form p {
  padding-bottom: 5px;
}
.next-btn {
  background: #0b1dd5;
  border-radius: 4px;
  padding: 7px 20px;
  color: #fff;
  border: none;
}

@media only screen and (max-width: 414px) {
  .product-detail {
    padding: 2.4rem 2rem 0 2rem;
  }
  form {
    margin-bottom: 6rem;
  }
  .product-d-box {
    max-width: 100%;
    margin: 10px auto;
    padding: 48px 12px;
    background: #f0f2fe;
    border-radius: 4px;
  }
}

